/*! input.less */

input[type=checkbox],
input[type=radio],
input[type=file] {
	display: none;
}

/* 设置 placeholder 字体颜色*/
	
input {
	color: @input-color;
	border: 0;
	outline: 0;
	border-radius: 0;
	display: inline-block;
	width: 100%;
	.placeholder (@placeholder-color, @placeholder-font-size);
}
input[type=number] {
	-moz-appearance: textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none !important;
	appearance: none;
}
